.title {
    text-align: center;
    margin-top: 30px;
    margin-left: auto;
    margin-right: auto;
}

.canvas_wrapper {
    max-height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.canvas {
    display: block;
    width: auto;
    height: 100%;
    max-height: 90vh;
    max-width: 100%;
}

.button_area {
    display: flex;
    justify-content: center;
    gap: 1rem;
}

